<template>
  <div class="tapStyle secondary-page-mode">
     <div class="secondary-toolbar">
          <span>时间选择 : </span>&nbsp;
          <div class="layoutBox" style="width:242px">
            <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </div>&emsp;&emsp;
          <el-button @click="search"><i class="iconfont icon-sousuo"></i>查询</el-button>
          <el-button type='delet'><i class="iconfont icon-daochu"></i>导出</el-button>
     </div>
     <div class="currency-mode-wrap">
          <el-table
              :data="tableData"
              border
              style="width: 100%">
              <el-table-column
                type="index"
                :index="indexMethod"
                width="50">
              </el-table-column>
              <el-table-column
                prop="name"
                label="点位名称">
              </el-table-column>
              <el-table-column
                prop="name"
                label="点位编码">
              </el-table-column>
               <el-table-column
                prop="name"
                label="所在省份">
              </el-table-column>
               <el-table-column
                prop="name"
                label="地区">
              </el-table-column>
               <el-table-column
                prop="name"
                label="所在流域">
              </el-table-column>
               <el-table-column
                prop="name"
                label="河流">
              </el-table-column>
              <el-table-column
                prop="name"
                label="运维单位">
              </el-table-column>
               <el-table-column
                prop="name"
                label="包件">
              </el-table-column>
               <el-table-column
                prop="name"
                width="140"
                label="仪器参数更改次数">
              </el-table-column>
              <el-table-column
                prop="name"
                label="巡检次数">
              </el-table-column>
               <el-table-column
                prop="name"
                width="170"
                label="试剂及标准样品更换次数">
              </el-table-column>
               <el-table-column
                prop="name"
                width="140"
                label="耗材及配件更换次数">
              </el-table-column>
               <el-table-column
                prop="name"
                width="140"
                label="仪器设备检修次数">
              </el-table-column>
               <el-table-column
                prop="name"
                label="访客登记次数">
              </el-table-column>
               <el-table-column
                prop="name"
                label="废液处理">
              </el-table-column>
          </el-table>
     </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      dateRange: "",
      tableData: [],
    };
  },
  created() {
    this.$store.commit("setShowlefttree", true);
  },
  methods: {
    indexMethod(index) {
      return index + 1;
    },
    search() {
     
    }
  }
};
</script>

<style scoped lang="less">

</style>



